inter;\n  transition: all ease-in-out 0.15s;\n}\n\n.imageConteiner {\n  position: absolute;\n  width: 364px;\n  height: 372px;\n  z-index: 0;\n  // background: linear-gradient(270deg, #b33327 0.15%, #d93e30 100%);\n  box-shadow: 4px 4px 8px rgba(1, 28, 64, 0.2);\n}\n\n.pictureWrap {\n  width: 341px;\n  height: 348px;\n  position: absolute;\n  right: 0;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n\n  img {\n    width: 100%;\n  }\n}\n\n.labelWrap {\n  display: flex;\n  justify-content: flex-start;\n  align-items: center;\n\n  position: absolute;\n  bottom: 15px;\n  right: 25px;\n}\n\n.label {\n  box-shadow: inset 0px -2px 0px rgba(0, 0, 0, 0.18);\n  border-radius: 11px;\n  background: $primary;\n  width: 73px;\n  height: 16px;\n  min-width: 60px;\n  @include font(normal, normal, 12px, 14px, $dark);\n  text-align: center;\n  padding: 0 12px 2px;\n  margin-right: 12px;\n\n  display: flex;\n  justify-content: center;\n  align-items: center;\n\n  &:last-child {\n    margin-right: 0;\n  }\n}\n\n.infoWrap {\n  width: 432px;\n  height: 372px;\n  position: absolute;\n  right: 0;\n\n  display: grid;\n  grid-template-rows: 96px 80px 90px 90px;\n}\n\n.textWrap {\n  position: relative;\n\n  display: flex;\n  justify-content: flex-end;\n  align-items: center;\n}\n\n.nameConteiner {\n  width: 169px;\n  height: 30px;\n\n  display: flex;\n  justify-content: flex-end;\n  align-items: center;\n\n  margin-left: 15px;\n\n  @include font(normal, 700, 30px, 36px, $white);\n}\n\n.generationContainer {\n  width: 140px;\n  height: 30px;\n\n  display: flex;\n  justify-content: flex-end;\n  align-items: center;\n\n  margin-right: 20px;\n  margin-left: auto;\n\n  @include font(normal, 400, 24px, 30px, $white);\n}\n\n.statsSum {\n  width: 41px;\n  height: 41px;\n  border-radius: 50%;\n  overflow: hidden;\n\n  display: flex;\n  justify-content: center;\n  align-items: center;\n\n  @include font(normal, normal, 15px, 18px, $dark);\n\n  background-color: $third;\n\n  margi